import React from 'react';
import { Routes, Route, useNavigate, Link } from 'react-router-dom';
import AdminUsers from './AdminUsers';
import AdminAnalytics from './AdminAnalytics';
import AdminAltars from './AdminAltars';
import AdminFlags from './AdminFlags';
import AdminSubscriptions from './AdminSubscriptions';
import AdminPayments from './AdminPayments';

function AdminNavBar() {
  const navigate = useNavigate();
  const handleBackToHome = () => {
    navigate('/home');
  };
  return (
    <nav style={{ background: '#2a509c', color: 'white', padding: 16, display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
      <div style={{ fontWeight: 'bold', fontSize: 24 }}>Admin Dashboard</div>
      <div style={{ display: 'flex', gap: 24 }}>
        <Link to="/admin/users" style={{ color: 'white', textDecoration: 'none', fontWeight: 500 }}>Users</Link>
        <Link to="/admin/altars" style={{ color: 'white', textDecoration: 'none', fontWeight: 500 }}>Altars</Link>
        <Link to="/admin/flags" style={{ color: 'white', textDecoration: 'none', fontWeight: 500 }}>Flags</Link>
        <Link to="/admin/subscriptions" style={{ color: 'white', textDecoration: 'none', fontWeight: 500 }}>Subscriptions</Link>
        <Link to="/admin/payments" style={{ color: 'white', textDecoration: 'none', fontWeight: 500 }}>Payments</Link>
        <Link to="/admin/analytics" style={{ color: 'white', textDecoration: 'none', fontWeight: 500 }}>Analytics</Link>
        <button onClick={handleBackToHome} style={{ background: '#fff', color: '#2a509c', border: 'none', borderRadius: 8, padding: '6px 16px', fontWeight: 'bold', cursor: 'pointer', marginLeft: 16 }}>Back to Home</button>
      </div>
    </nav>
  );
}

function Placeholder({ title }) {
  return <div style={{ padding: 32, fontSize: 24, color: '#2a509c' }}>{title} coming soon...</div>;
}

export default function AdminDashboard() {
  return (
    <div style={{ minHeight: '100vh', background: '#e0f2ff' }}>
      <AdminNavBar />
      <Routes>
        <Route path="users" element={<AdminUsers />} />
        <Route path="altars" element={<AdminAltars />} />
        <Route path="flags" element={<AdminFlags />} />
        <Route path="subscriptions" element={<AdminSubscriptions />} />
        <Route path="payments" element={<AdminPayments />} />
        <Route path="analytics" element={<AdminAnalytics />} />
        <Route path="*" element={<Placeholder title="Welcome to the Admin Dashboard" />} />
      </Routes>
    </div>
  );
} 